			//     str = (" ORDER BY " + str1);
			//     strCmd += str;
			// }// Tiene sort
			// //
			// // Preparar las posibles macros globales
			// // F12042601: PrepareSqlString no delega en la aplicación cuando se llama a la colección.
			// strCmd = this.PrepareSqlString(strCmd, false, bPrefiltered);
